.toggle {
  text-align: center;
  margin-bottom: 1rem;
}
.toggle button {
  margin: 0 0.5rem;
  padding: 0.3rem 0.6rem;
  cursor: pointer;
}
.cardGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}
.listGrid {
  display: flex;
  flex-direction: column;
}
